body {
  font-size: 0.9rem;
}

.App-logo {
  height: 50px;
}

.App-header {
  background-color: white;
  height: 120px;
  padding: 20px;
  color: black;
  text-align: center;
}

.App-title {
  font-size: 1.2em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



